import Users from "@/components/Users";
import { Suspense } from "react";

export default function page() {
  const users = fetch("http://localhost:4000/api/users").then((res) =>
    res.json()
  );

  return (
    <Suspense fallback={<div>Loading...</div>}>
      <Users users={users} />
    </Suspense>
  );
}
